<template>
    <div class="field">
        <label :for="val">{{ val }}</label>
        <textarea :id="val" cols="130" rows="3" :placeholder="val" v-model="model" ref="textarea" @input="updateSection(this.model)"></textarea>
    </div>
</template>

<script>
export default {
    name: 'Field',
    props: {
        val: String,
    },
    data() {
        return {
            model: '',
        }
    }, 
    methods: {
        updateSection(text) {
            let section = this.val[0].toLowerCase() + this.val.slice(1).replace(' ', '');
            console.log(text);
            this.$store.dispatch('updateSection', [section, text]);
        }
    }
}
</script>

<style lang="scss">
    label {
        font-weight: bold;
        display: block;
        font-family: Verdana, Geneva, Tahoma, sans-serif
    }

    textarea, input[type="text"] {
        margin-bottom: 1.5em;
        font-family: Verdana, Geneva, Tahoma, sans-serif;
        border-radius: 3px;
        resize: none;
    }
</style>