import { createStore } from 'vuex'

export default createStore({
	state: {
		pokemon: '',
		availability: '',
		typing: '',
		stats: '',
		movepool: '',
		majorBattles: '',
		additionalComments: '',
		exceptions: {
			abra: {
				yestrade: ['alakazam', 'Abra (Trade)'],
				notrade: ['kadabra', 'Abra (No Trade)'],
			},
			geodude: {
				yestrade: ['golem', 'Geodude (Trade)'],
				notrade: ['graveler', 'Geodude (No Trade)'],
			},
			machop: {
				yestrade: ['machamp', 'Machop (Trade)'],
				notrade: ['machoke', 'Machop (No Trade)'],
			},
			gastly: {
				yestrade: ['gengar', 'Gastly (Trade)'],
				notrade: ['haunter', 'Gastly (No Trade)'],
			},
			eeveelutions: {
				jolteon: ['jolteon', 'Eevee (Jolteon)'],
				vaporeon: ['vaporeon', 'Eevee (Vaporeon)'],
				flareon: ['flareon', 'Eevee (Flareon)'],
			}
		}
	},
	mutations: {
		updateSection(state, section) {
			let category = section[0];
			let value = section[1];
			value = value.replace(/â€”/g, '&mdash;');
			if (!value.endsWith('.')) {
				value += '.';
			}
			state[category] = value;
		},
		restartForm(state) {
			for (let section in state) {
				state[section] = '';
			}
		},
		updatePokemon(state, pokemon) {
			state.pokemon = pokemon;
		}
	},
	actions: {
		updateSection(context, section) {
			context.commit('updateSection', section);
		},
		restartForm(context) {
			context.commit('restartForm');
		},
		updatePokemon(context, pokemon) {
			context.commit('updatePokemon', pokemon);
		} 
	},
	modules: {
	}
})
