<template>
    <label for="pokemon">Pokemon</label>
    <input type="text" placeholder="Pokemon name" v-model="this.name" @input="updateName(this.name)" id="pokemon">
</template>

<script>
export default {
    name: 'Pokemon',
    data() {
        return {
            name: '',
        }
    },
    methods: {
        updateName(mon) {
            let lowercased = '';
            let tradePokemon = '';
            if (this.name) {
                lowercased = mon.toLowerCase().replace(/\s/g, '');
            }

            if (lowercased.includes('(trade)')) {
                tradePokemon = lowercased.replace('(trade)', '');
                this.$store.dispatch('updatePokemon', this.$store.state.exceptions[tradePokemon].yestrade);
            } else if (lowercased.includes('(notrade)')) {
                tradePokemon = lowercased.replace('(notrade)', '');
                this.$store.dispatch('updatePokemon', this.$store.state.exceptions[tradePokemon].notrade);
            } else if (lowercased.includes('(') && lowercased.endsWith(')')) {
                let eeveelution = lowercased.match(/(?<=\().*?(?=\)$)/)[0];
                this.$store.dispatch('updatePokemon', this.$store.state.exceptions.eeveelutions[eeveelution]);
            } else {
                this.$store.dispatch('updatePokemon', [lowercased, mon]);
            }
        }
    }
}
</script>

